import React from 'react'

const Intro = props => {

    let content = (
        <div className={props.className}>
            <div className="container h-full w-auto content-center mx-auto flex flex-wrap">
                <strong className="font-raleway font-bold text-sm text-gray-900">From </strong>
                <div
                    className="h-4 w-4 flex items-center justify-center ml-1 mr-1">
                    <img src='icons/morocco.png' />
                </div>
                <strong className="font-raleway font-bold text-sm text-gray-900"> to world wide</strong>
            </div>
        </div>
    )

    return content
}

export default Intro